.header-color{
    background: #0F2027;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid{
    background: #0F2027;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position: fixed;
top:0;
left:0;
height:100%;
width:100%;
}
.avatar-img {
    height: 250px;
    padding-top: 5em;
    justify-content: space-between;
  }
.banner-text{
    background-color: black;
    opacity: .8;
    width: 60%;
    margin: auto;
    border-radius: 10px;
}
.banner-text h1{
    font-size: 60px;
    font-weight: bold;
    color: white;
}
.banner-text hr{
    border-top: 5px dotted white;
    width: 40%;
    margin: auto;
    
}
.banner-text p{
    color: white;
    font-size: 20px;
    padding: 1em;
}
.social-links{
    display:flex;
    margin: auto;
    justify-content: space-between;
    width: 50%;

}
.social-links i{
    color:white;
    font-size: 5em;
    padding: 8px;

}
.projects-grid{
    display:flex;
}